.statistics {
  &__content {
    @include flex;

    position: relative;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__left {
    max-width: 800px;
  }

  &__title {
    color: var(--green-color);

    &--mb {
      margin-bottom: 40px;
    }
  }

  &__circle-wrapper {
    position: relative;
    margin-bottom: 66px;
    width: 800px;
    height: 700px;
  }

  &__circle-img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }

  &__text-wrapper {
    position: absolute;
    left: 55%;
    top: 50%;
    text-align: center;
    color: var(--light-color);
    transform: translate(-50%, -50%);
  }

  &__text {
    font-weight: 700;
    font-size: 36px;

    &--smaller {
      margin-bottom: 39px;
      font-size: 25px;
    }
  }

  &__val {
    position: absolute;
    font-size: 30px;

    &--1 {
      left: -9px;
      top: -124px;
    }

    &--2 {
      right: 93px;
      top: -158px;
    }

    &--3 {
      right: -102px;
      top: 35px;
    }

    &--4 {
      right: 95px;
      bottom: -195px;
    }

    &--5 {
      left: -100px;
      bottom: 0;
    }
  }

  &__list {
    @include flex;

    margin-bottom: 20px;
  }

  &__item {
    @include mr(34px);

    border-radius: 10px;
    padding: 25px;
    text-align: center;
    background-color: rgba(0 0 0 / 50%);
  }

  &__item-title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 20px;
    color: var(--green-color);
  }

  &__item-text {
    font-weight: 700;
    font-size: 55px;
    color: var(--light-color);
  }

  &__left-text {
    font-family: "PFBagueSansPro", sans-serif;
    font-size: 20px;
    color: var(--light-color);
  }

  &__right {
    max-width: 255px;
  }

  &__right-content {
    @include mb(100px);
  }

  &__right-title {
    margin-bottom: 33px;
    line-height: 1.1;
  }

  &__right-item {
    position: relative;
    font-family: "PFBagueSansPro", sans-serif;
    font-weight: 400;
    font-size: 27px;
    color: var(--light-color);

    &:not(:last-child) {
      margin-bottom: 38px;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -21px;
        width: 100%;
        height: 1px;
        background-color: #64761f;
      }
    }
  }
}
