.token {
  @include bg-cover;

  position: relative;
  margin-top: -115px;
  min-height: 1900px;
  background-image: url("../img/token/token-bg.png");
  background-position: center -100px;

  &__content {
    padding: 165px 0 122px;
  }

  &__content-wrapper {
    max-width: 615px;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__content-descr {
    margin: 0;
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 1.25;
    color: var(--green-color);
  }

  &__content-text {
    font-family: "PFBagueSansPro", sans-serif;
    font-weight: 400;
    font-size: 20px;
    color: var(--light-color);
  }

  &__img {
    position: absolute;

    &--parachute-reverse {
      right: 163px;
      top: -11px;
    }

    &--hero {
      left: 464px;
      bottom: -93px;
    }
  }
}
