.hero {
  @include bg-cover;

  position: relative;
  z-index: 1;
  padding: 320px 0;
  background-image: url("../img/hero/hero-bg.png");
  background-position: center bottom;

  &__title {
    font-weight: 700;
    font-size: 55px;
    line-height: 1.25;
    text-transform: uppercase;
    color: var(--light-color);
  }

  &__img {
    position: absolute;

    &--parachute {
      left: 47px;
      top: 52px;
    }

    &--hero-1 {
      left: 541px;
      top: 140px;
    }

    &--hero-2 {
      left: 138px;
      bottom: 50px;
    }

    &--hero-3 {
      right: -20px;
      bottom: -5px;
    }
  }

  &__text {
    max-width: 980px;
  }
}
